<template>
    <div>
    
            <HeaderTop title="个人中心">
            </HeaderTop>
      
        <div class="profile">
            <ul>
                <li>
                    <img src="" alt="">
             
                    <span v-if="login">dsdsds</span>
                    <span v-if="login">159****9834</span>
                    <span v-if="!login" class="but_login" @click="to_login()">登录/注册</span>

                
                </li>
                <li></li>
                <li>我的红包</li>
                <li>我的津贴</li>
                <li>我的地址</li>
                <li>我的收藏</li>
                <li>我的客服</li>
                <li>规则中心</li>
               
            </ul>
        </div>
    </div>
</template>

<script>
import HeaderTop from '../../components/HeaderTop/HeaderTop';
import {mapState,mapActions} from 'vuex';

export default {
    data(){
        return {

        }
    },
    computed:{
     ...mapState(['login'])
    },
    components:{
        HeaderTop
    },
    methods:{
        ...mapActions(['footer_on']),
       to_login(){
           this.$router.replace('./login');
           this.footer_on(false);
       }
    }
}
</script>

<style scoped>
 .profile{
     width:100%;
 
     position:absolute;
    top: 30px;
     left:0;

 }
 .profile ul{
     width:93%;
     margin: 0 auto;
 }
 .profile li{
     width: 100%;
     height: 50px;
     border-bottom: 1px solid silver;
     line-height: 45px;
     font-size: 15px;
 }
 .profile li:first-child{
     height: 110px;
     border:none;
     
 }
 .profile li:first-child img{
     width: 80px;
     height: 80px;
     border:1px solid black;
     display: inline-block;
     margin-top: 10px;
     border-radius: 50%;
     overflow: hidden;
 }
 .profile li:first-child span{
     position: absolute;
     top:20px;
     margin-left: 10px;
     font-size: 28px;
 }
  .profile li:first-child span:nth-of-type(2){
      top:55px;
      font-size: 10px;
  }
    .profile li:first-child .but_login{
        width: 110px;
        height: 40px;
        background: darkseagreen;
        border-radius: 5px;
        font-size: 17px;
        color: white;
        text-align: center;
        line-height: 40px;
        margin:23px 0 0 15px;

    }
 .profile li:nth-of-type(2){
     height: 90px;
     border-radius: 10px;
     background-color:darkcyan;
     border:none;
     margin-bottom: 10px;
 }
#warpper{
    width: 100%;
    height: 30px;
    overflow: hidden;
    border:1px solid black;
}
</style>


