<template>
    <div class="order">
   <HeaderTop title="订单中心">
   </HeaderTop>
        <header class="header">
            <a href="" class="header_title">
                <span class="header_title_text">
                    订单列表
                </span>
            </a>
        </header>
        <div class="order_no_login">
         
            <h3>登录后查看订单状态</h3>
             <button @click="fn()">立即登录</button>
        </div>
    </div>
</template>

<script>
import HeaderTop from '../../components/HeaderTop/HeaderTop'
export default {
    components:{
        HeaderTop
    },
    methods:{
      fn(){
          this.$router.replace('/login')
      }
    }
}
</script>

<style scoped>
 
</style>


