        </div>
        <div class="swiper-pagination"></div>
      </div>
    </nav>
    <!--首页列表-->
    <shop-list />
  </div>
</template>

<script>
import Vue from 'vue';
import vuejsonp from 'vue-jsonp';
import HeaderTopInd from "../../components/HeaderTop/HeaderTop_ind.vue"
import Swiper from "swiper";
import "swiper/css/swiper.min.css";
import ShopList from "../../components/ShopList/ShopList.vue";
import axios from 'axios';
Vue.use(vuejsonp);

export default {
  data() {
    return {
      location:'',
      key:true
    };
  },
  mounted() {
    let ind = this;
    //创建swiper对象实现轮播
    new Swiper(".swiper-container", {
      loop: true, //可以循环轮播
      pagination: {
        el: ".swiper-pagination" //分页器
      }
    });
    
	this.$jsonp('https://apis.map.qq.com/ws/location/v1/ip',{key:'UGNBZ-AIR6D-2DV4Y-HVIHB-JI4OK-XBBCK',output:'jsonp'}).then(json=>{
    ind.location = json.result.ad_info.city+json.result.ad_info.district;
  }).catch(err=>{
    ind.location = '获取信息失败';
  });
		
  

    



 
  
  },

  
components: {
    HeaderTopInd,
    ShopList
  }
};
</script>

<style scoped>
.msite_nav {
  width: 100%;
  height: 200px;

  box-sizing: border-box;
  position: absolute;
  top: 90px;
  left: 0;
}
.msite_nav  img{
  width: 50px;
  height: 50px;
  border:1px solid black;
  display: block;
  margin:10px 0 0 25px;
}
.swiper-container {
  width: 100%;
  height: 200px;
}
.swiper-slide {
  width: 100%;
  height: 200px;
  
}
.swiper-slide a {
  width: 25%;
  height: 90px;

  float: left;
  text-align: center;
}
.header_list {
  width: 100%;
  height: 40px;
}
.log{
  display: inline-block;
  width: 30px;
  height: 30px;
  animation: upto 4s infinite;
}
@keyframes  upto{
  0%{
    transform: translateX(0px);
  }
  50%{
    transform: translateX(10px);
    
  }
  75%{
     transform: translateX(0px);
      
  }
  100%{
    transform: translateX(-10px);
  }
}
</style>
 



