<template>
 
 <header class="msite_header">
          <slot name="left"></slot>
          <span class="header_title">
              <span class="header_title_taxt">{{title}}</span>
          </span>
          <slot name="right"></slot>
       </header>

</template>

<script>
export default {
    props:{
        title:String
    }
}
</script>

<style scoped>
.msite_header{
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 30px;
   background-color: mediumseagreen;
   line-height: 30px;
   color:white;

}
.header_search{
    float: left;
    margin-left: 10px;
   
}
 .header_search .iconfont{
      font-size: 20px;
 }
 .header_login{
     float:right;
     margin-right: 10px;
 }
 .header_title{
     position: absolute;
     left: 50%;
     transform: translateX(-60px);
     width: 120px;
    text-align: center;
     display: inline-block;
     overflow: hidden;
     text-overflow: ellipsis;
     white-space: nowrap;
     
 }
</style>
 

 



