<template>
    <div  class="login_ind"> 
      <div class="login_top">
          <i class="iconfont icon-jiantou2" @click="go_back()"></i>
      </div>
      <div class="login_body">
          <form action="#">
              <span class="up" :class="{xian:show}" @click="up_on()">密码登录</span>
              <span class="ph" :class="{xian:!show}" @click="ph_on()">手机登录/注册</span>
               <div class="up_go" v-if="show">
              <input type="text" placeholder="请输入用户名" name="username">
              <input type="password" placeholder="请输入密码" name="password">
              <input type="text" placeholder="验证码" name="yz">
              <span class="yz">验证码</span>
              <input type="submit">
               </div>
                <div class="ph_go" v-if="!show">
              <input type="text" placeholder="请输入手机号" name="username">
              <span class="yz_warpper">
                  <input type="yz" placeholder="请输入验证码" name="yz">

              <span class="yz">获取验证码</span>
              </span>
              <input type="submit">
               </div>
              
          </form>
      </div>      
    </div>
</template>

<script>

import {mapState,mapActions} from 'vuex';
export default {
    data(){
        return {
         show:true
        }
    },
    computed:{
       ...mapState(['login'])
    },
    components:{
      
    },
    methods:{
        ...mapActions(['to_login','footer_on']),
        go_back(){
            this.$router.replace('./msite');
             this.footer_on(true);
        },
        ph_on(){
            this.show = false;
        },
        up_on(){
            this.show = true;
        }
    }
}
</script>

<style scoped>
 .iconfont{
     font-size: 32px;
     margin: 10px 0 0 5px;
     color:darkorange;

 }
 .login_body{
     width: 100%;
     height:100%;
     background: white;
     position: relative;
 }
 span{
     display: inline-block;
 }
 .login_body form{
     width: 70%;
     height: 300px;
     margin: 0 auto;
   
     position: relative;
    
  
 }
 .login_body .up,
 .ph{
     position: absolute;
  
     top:40px;

     height: 40px;
    font-size:16px;
    padding: 5px;
    box-sizing: border-box;
    line-height: 40px;
     
 }
 .ph{
  left:140px;
 }
 .xian{
     border-bottom: 1px solid black;
 }
.login_body input{
    margin-bottom:10px;
    padding: 5px;
    box-sizing: border-box;
    width: 250px;
    height: 30px;
}
.login_body form div{
    position: absolute;
   top:90px;
    width: 100%;
    height: 200px;
   
}
.login_body form .up_go input:nth-of-type(3){
   width: 80px;
}
.login_body form .ph_go .yz_warpper input{
    width: 100px;
outline: none;
border:none;
}
.login_body form .ph_go .yz_warpper{
    width: 250px;
    height: 32px;
    border:1px solid darkgrey;
    background: white;
    margin-bottom: 10px;
    line-height: 32px;
}
.login_body form .ph_go .yz_warpper span{
    float: right;
    margin-right: 4px;
    color: aqua;
    
}


</style>


