<template>
 
 <header class="msite_header">
        
      <slot name="left"></slot>
      <span class="header_title">
              <span class="header_title_taxt">{{title}}</span>
          </span>
      <div class="sousuo">
          <input type="text" placeholder="请输入搜索内容">
          <i class="iconfont icon-sousuo"></i>
      </div>
      <span class="header_login" >
        <span class="header_login_text" @click="login()">登录/注册</span>
      </span>
</header>

</template>

<script>
import {mapState,mapActions} from 'vuex';
export default {
    props:{
        title:String
    },
    methods:{
        ...mapActions(['footer_on']),
        login(){
         this.$router.replace('./login');
         this.footer_on(false);
        }
    }
}
</script>

<style scoped>
.msite_header{
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 90px;
   background-color: mediumseagreen;
   line-height: 30px;
   color:white;
    z-index: 999;
   
}
.header_search{
    float: left;
    margin-left: 10px;
   
}
 .header_search .iconfont{
      font-size: 20px;
 }
 .header_login{
     float:right;
     margin-right: 10px;
 }
 .header_title{
     position: absolute;
     left: 50%;
     transform: translateX(-60px);
     width: 120px;
    text-align: center;
     display: inline-block;
     overflow: hidden;
     text-overflow: ellipsis;
     white-space: nowrap;
     
 }
 .sousuo {
     width: 80%;
     height: 30px;
     border:1px solid black;
     position: absolute;
     left: 10%;
     top:50%;
     
    border-radius: 20px;
    border-color: seashell;
 

 }
 .sousuo input{
     
     height: 25px;
     width: 80%;
     border:none;
     background: none;
     padding: 0 0 8px 12px;
     position: absolute;
     font-size: 15px;
     
 }
  .sousuo .iconfont{
     position: absolute;
     right: 10px;
     color:sandybrown;
  }
</style>
 



